export default {
  props: {
    name: {
      type: String,
      required: true,
    },
    flag: {
      type: Boolean,
      required: false,
    },
    smallIcon: {
      type: Boolean,
      required: false,
    },
    title: {
      type: String,
      required: false,
    },
    className: {
      type: String,
      required: false,
    },
  },
  computed: {
    src() {
      const src = require(`~/static/sprite/svg/${this.name}.svg?raw`)
      return src
    },
  },
}
