export default {
  data() {
    return {
      dialog: false,
    }
  },
  props: {
    width: {
      type: [Number, String],
      required: false,
    },
    notificationName: {
      type: String,
      required: true,
    },
    data: {
      type: [Object, Array],
      required: false,
    },
    className: {
      type: String,
      required: false,
    },
  },
  methods: {
    onDialogCardToggled: function (event) {
      this.dialog = !this.dialog
    },
  },
}
